.ActivityDetails {
  .Activity--Items {
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    .AmountItem {
      height: 25px;
      font-size: 14px;
      padding: 2px;
      line-height: 25px;
      color: #87909A;

      .AmountName {
        float: left;
      }

      .Amount {
        float: right;
      }
    }
    .Activity--Total {
      height: 25px;
      font-size: 14px;
      padding: 2px;
      line-height: 25px;
      font-weight: bold;
      border-top: 1px solid #87909A;

      .Activity--TotalAmountCaption {
        float: left;
      }

      .Activity--TotalAmount {
        float: right;
      }
    }
  }
}